import React from 'react'
import axios from 'axios'
import { useEffect, useState } from 'react'
import { Row, Col, Card, Form, InputGroup, Button } from 'react-bootstrap'

const Homepage = () => {
  const apiKey=process.env.REACT_APP_API_KEY;
  const [documents, setDocuments] = useState([]);
  const [query, setQuery] = useState("");
  const [page, setPage] = useState(1);
  const [lastPage, setLastPage] = useState(1);
  const callAPI = async() => {
    const url = "https://dapi.kakao.com/v3/search/book?target=title"
    const config = {
      "headers": {
        "Authorization": "KakaoAK cc7e003ea763f92f3084120e7b61e44b"
      },
      params: {
        query: query,
        size:12,
        page:page
      }
    }
    
    const res = await axios.get(url, config)
    setDocuments(res.data.documents);
    setLastPage(Math.ceil(res.data.meta.pageable_count/12));
  }

  useEffect(() => {
    const titleElement = document.getElementsByTagName('title')[0];
    titleElement.innerText = "";
    callAPI();
  }, [page]);

  const onSubmit = (e) => {
    e.preventDefault();
    if (query===''){
      alert(" ");
    } else {
      callAPI();
    }
  }
  return (
    <div>
      <h1 className='my-5 text-center'></h1>
      <Row className='mb-2'>
        <Col>
          <Form onSubmit={onSubmit}>
            <InputGroup>
              <Form.Control onChange={(e)=>setQuery(e.target.value)} 
                value={query}/>
              <Button type='submit'></Button>
            </InputGroup>
          </Form>
        </Col>
        <Col></Col>
        <Col></Col>
      </Row>
      <Row>
        {documents.map(doc=>
          <Col lg={2} md={3} xs={6}>
            <Card>
              <Card.Body>
                  <img src={doc.thumbnail} width="100%"/>
                </Card.Body>
              <Card.Footer>
                <div className='text-truncate'>{doc.title}</div>
              </Card.Footer>
            </Card>
          </Col>
        )}
      </Row>
      <div className='text-center mt-3'>
        <Button 
          disabled={page===1}
          onClick={()=>setPage(page-1)}></Button>
        <span>{page}</span>
        <Button 
          disabled={page===lastPage}
          onClick={()=>setPage(page+1)}></Button>
      </div>
    </div>
  )
}

export default Homepage
